import { useEffect, useState } from 'react'

import type { SelectChangeEvent } from '@mui/material'
import {
  Box,
  Button,
  FormControl,
  Grid,
  InputLabel,
  MenuItem,
  Select,
  TextField,
  Typography,
  styled
} from '@mui/material'

import CLUB_BELONGING from 'src/data/belonging'
import CLUB_CATEGORIES from 'src/data/categories'

import type { ClubId } from 'src/models/club'

import { useGetClubInformation } from 'src/hooks/api/club'
import type { ClubInformationRequestData } from 'src/hooks/api/settings'
import { useEditClubInfo } from 'src/hooks/api/settings'

const ClubInformation: React.FC<{ clubId: ClubId }> = ({ clubId }) => {
  const { data: clubData, isSuccess } = useGetClubInformation(clubId)

  const [clubInformation, setClubInformation] = useState<ClubInformationRequestData>({
    description: '',
    category: '',
    belonging: '',
    location: ''
  })

  const editClubLinks = useEditClubInfo()

  // TODO: useEffect보다 더 효과적인 방법 구현하기..
  useEffect(() => {
    if (isSuccess && clubData) {
      setClubInformation({
        category: clubData.category ?? '',
        belonging: clubData.belonging ?? '',
        description: clubData.description ?? '',
        location: clubData.location ?? ''
      })
    }
  }, [isSuccess, clubData])

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target
    setClubInformation(prevState => ({
      ...prevState,
      [name]: value
    }))
  }

  const handleSelect = (e: SelectChangeEvent<string>) => {
    const { name, value } = e.target
    setClubInformation(prevState => ({
      ...prevState,
      [name]: value
    }))
  }

  const handleClickEditButton: React.MouseEventHandler<HTMLButtonElement> = () => {
    if (isSuccess) {
      editClubLinks.mutate({ clubId, clubInfo: clubInformation })
    }
  }

  return (
    <Box mb={2}>
      <Box width={'100%'}>
        <Typography variant={'h4'}>동아리 기본 정보 설정</Typography>
        <Grid container alignItems="center" mt={2} spacing={2}>
          <ItemGrid item xs={12}>
            <TextField
              fullWidth
              label="동아리명"
              name="name"
              id="name"
              value={clubData?.name ?? ''}
              disabled
              helperText={'동아리명은 변경이 불가능합니다'}
            />
          </ItemGrid>
          <ItemGrid item xs={12} sm={6}>
            <TextField
              label="동아리 설명"
              required
              name="description"
              id="description"
              fullWidth
              value={clubInformation.description}
              onChange={handleChange}
            />
          </ItemGrid>

          <ItemGrid item xs={12} sm={6}>
            <TextField
              label="동아리 장소"
              name="location"
              id="location"
              fullWidth
              value={clubInformation.location}
              onChange={handleChange}
            />
          </ItemGrid>
          <ItemGrid item xs={6}>
            <FormControl fullWidth>
              <InputLabel id="belonging-label">소속</InputLabel>
              <Select
                labelId="belonging-label"
                id="belonging"
                name="belonging"
                label="소속"
                value={clubInformation.belonging}
                onChange={handleSelect}
              >
                <MenuItem value="">선택하지 않음</MenuItem>
                {CLUB_BELONGING &&
                  CLUB_BELONGING.map((name, idx) => (
                    <MenuItem key={idx} value={name}>
                      {name}
                    </MenuItem>
                  ))}
              </Select>
            </FormControl>
          </ItemGrid>
          <ItemGrid item xs={6}>
            <FormControl fullWidth>
              <InputLabel id="category-label">동아리 카테고리</InputLabel>
              <Select
                labelId="category-label"
                id="category"
                name="category"
                label="카테고리"
                value={clubInformation.category}
                onChange={handleSelect}
              >
                <MenuItem value="">선택하지 않음</MenuItem>
                {CLUB_CATEGORIES &&
                  CLUB_CATEGORIES.map((name, idx) => (
                    <MenuItem key={idx} value={name}>
                      {name}
                    </MenuItem>
                  ))}
              </Select>
            </FormControl>
          </ItemGrid>
          <ItemGrid item xs={12} justifyContent={'flex-end'}>
            <Button variant="contained" color="success" onClick={handleClickEditButton}>
              등록 / 수정
            </Button>
          </ItemGrid>
        </Grid>
      </Box>
    </Box>
  )
}

export default ClubInformation

const ItemGrid = styled(Grid)({
  // display: { xs: 'flex' }, justifyContent: { xs: 'center', sm: 'flex-start'
  display: 'flex'
})
